.hero {
    position: relative;

    &__items {}

    &__item {
        &>.container {
            min-height: 90vh;

            // @media (max-width: 1199px) {
            //    min-height: 75vh;
            // }
            
        }

        background-size: cover;
        background-position: center;
    }

    &__title {
        color: white;
        font-size: 60px;
        font-weight: bold;
        line-height: 1.3;

        @media (max-width: 1199px) {
           font-size: 48px;
        }

        @media (max-width: 991px) {
           font-size: 35px;
        }
        
        
    }

    &__description {
        color: white;
    }
}